@import "../variables";

.loader {
	height: $loader-height;
	width: 100%;
	margin: 0;

	background-color: $loader-background;
	display: -webkit-flex;
	display: flex;

	&:before {
		height: $loader-height;
		width: 100%;
		margin: 0;

		background-color: $loader-color;
		content: '';
		-webkit-animation: loader-indefinite $loader-speed cubic-bezier(0.4, 0, 0.2, 1) infinite;
		animation: loader-indefinite $loader-speed cubic-bezier(0.4, 0, 0.2, 1) infinite;
	}
}

@-webkit-keyframes loader-indefinite {
	0% {
		margin-left: 0px;
		margin-right: 100%;
	}

	50% {
		margin-left: 25%;
		margin-right: 0%;
	}

	100% {
		margin-left: 100%;
		margin-right: 0;
	}
}

@keyframes loader-indefinite {
	0% {
		margin-left: 0px;
		margin-right: 100%;
	}

	50% {
		margin-left: 25%;
		margin-right: 0%;
	}

	100% {
		margin-left: 100%;
		margin-right: 0;
	}
}